<template>
  <user>
    <html-head 
      title="Add User"
    ></html-head>

    <page-header>
      <template v-slot:title >
        <span v-text="`Add User`"></span>
      </template>
    </page-header>

    <v-row>
      <v-col cols="12" sm="8" lg="6" class="mx-auto">
        <validation-observer ref="validation">
        <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="user.isFetching">
          <v-card elevation="0">
            <v-card-title v-text="`Add User`"></v-card-title>
            <v-card-text>
              <form-errors :error="user.errors.message"></form-errors>
              <validation-provider vid="firstName" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="First Name"
                  name="firstName"
                  outlined
                  v-model="user.data.firstName"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="lastName" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Last Name"
                  name="lastName"
                  outlined
                  v-model="user.data.lastName"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="email" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="E-mail"
                  name="email"
                  outlined
                  v-model="user.data.email"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="username" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Username"
                  name="username"
                  outlined
                  v-model="user.data.username"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="password" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Password"
                  name="password"
                  type="password"
                  outlined
                  v-model="user.data.password"
                  @input="user.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="role" v-slot="{ errors }">
                <role-selector v-model="user.data.role" @input="user.prestine( false )" :errors="errors"></role-selector>
              </validation-provider>
              <!-- <input type="hidden" name="creator" :value="auth.user._id"> -->
            </v-card-text>
            <v-card-actions class="d-flex justify-end"> 
              <v-btn color="primary" type="submit" :loading="user.isFetching" :disabled="user.isPrestine">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
        </validation-observer>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import User from './models/User'
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      auth: 'auth/auth',
    }),
  },

  data: () => ({
    user: new User,
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
    }),

    async submit () {
      const data = await this.user.request({
        method: 'post',
        url: Api.main(),
        data: this.user.readyData( this.$refs.form.$el ),
      })

      if( !data ) 
        return this.$refs.validation.setErrors( this.user.errors.details )
        
      this.setSnackbar({
        model: true,
        text: 'User created',
        timeout: 3000
      })

      this.$router.push({ name: 'users.edit', params: { id: data._id } })
    },
  },

  mounted () {
    this.user.fetch( false )
    this.user.load( false )
  }
}
</script>